import React, { useState } from 'react';
import Title from './comps/Title';
import UploadForm from './comps/UploadForm';
import ImageGrid from './comps/ImageGrid';
import Modal from './comps/Modal';
import Blog from './comps/Blog'
import MusicPlayer from "./comps/MusicPlayer";
import video from './video.mp4'
import Footer from './comps/Footer'
import { Wave } from 'react-animated-text';



function App() {
  const [selectedImg, setSelectedImg] = useState(null);

  return (
    <div className="App">
      
      
      <Title/>
      <Blog />
      <div className="wideo">
       <h4><Wave text="Your Marriage Video" effect="color" effectDirection="left" effectChange="red" effectDuration="1.0"/></h4> 
      <video src={video} controls="controls" style={{cursor:'pointer'}} className="videoo"/></div>
      <UploadForm />
      <ImageGrid setSelectedImg={setSelectedImg} />
      { selectedImg && (
        <Modal selectedImg={selectedImg} setSelectedImg={setSelectedImg} />
      )}
      <MusicPlayer/>
      <Footer />
    </div>
  );
}

export default App;
