import React from 'react'
import soundfile from './Playlist.mp3'
import ReactPlayer from "react-player";





function MusicPlayer() {
    
        let myInlineStyle={
            boxShadow:'0 2px 3px #FFF',
            border:'2px solid white',
            borderRadius:'80%',
            marginTop:'15px',
            marginBottom:'10px',
            marginLeft:'30%',
            cursor:'pointer',
            ':hover':{
               background:'#8f8484e3',
               cursor:'pointer',
            },
         }
         
    return (
        <div className="mplayer" >
             <ReactPlayer
        url={soundfile}
        borderRadius="80%"
        width="400px"
        height="20px"
        margin="auto"
        playing={true}
        controls={true}
        style={myInlineStyle}
        
      
        
      />
        </div>
    )
}


export default MusicPlayer


